@import url('https://fonts.googleapis.com/css2?family=Open+Sans:wght@300;400&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Playfair+Display:wght@700&display=swap');


/* ||||||||||||||||||||||||universal||||||||||||||||||||||||||||| */
*{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

body
{
  overflow-x: hidden!important;
}

a
{
  color: white;
  text-decoration: none;
}

.link
{
  color: #FFC34A;
  text-decoration: none;
}

.link:hover
{
  color: #ffd98e;
  text-decoration: none;
}


.admin-content {
    margin-top: 100px;
    margin-left: 260px;
    padding: 1.875em;
    width: auto;
}

.table>thead {
  vertical-align: middle;
}

.table>tbody {
  vertical-align: middle;
}

.table>tbody tr th{
  font-size: 15px;
  font-weight: 500;
  color: #464646;
}




@media screen and (max-width:786px) 
{
  .row {
    --bs-gutter-x: 0;
}  

.admin-content {
  margin-top: 100px;
  margin-left: 0px;
  padding: 1.875em;
  width: auto;
}

}


/* font-family: 'Open Sans', sans-serif; */
/* font-family: 'Playfair Display', serif; */



/* Modal */
.modal-container
{
  margin: 30px;
}

.updatebtn
{
    display: flex;
    justify-content: flex-end;
    margin-top: 47px;
    margin-left: -25px!important;
}


/* Sidebar */
.sidebar {
  position: fixed;
  top: 100px;
  bottom: 0;
  left: 0;
  box-shadow: 8px 3px 15px 0px gray;
  width: 240px;
  height: auto;
  }
  
  @media (max-width: 991.98px) {
  .sidebar {
  width: 100%;
  z-index: 100;
  height: max-content;
  }
  }
  .sidebar .active {
  border-radius: 5px;
  box-shadow: 0 2px 5px 0 rgb(0 0 0 / 16%), 0 2px 10px 0 rgb(0 0 0 / 12%);
  }
  
  .sidebar-sticky {
  position: relative;
  top: 0;
  height: calc(100vh - 48px);
  padding-top: 0.5rem;
  overflow-x: hidden;
  overflow-y: auto; /* Scrollable contents if viewport is shorter than content. */
  }


  #main-navbar
  {
    position: fixed;
  }


  .dropdown
  {
    list-style: none;
  }

  .dropdown-menu li{
    padding: 10px;
  }

  .dropdown-menu[data-bs-popper] {
    top: 100%;
    left: -30px;
    margin-top: var(--bs-dropdown-spacer);
  }

  .dropdown-menu {
    --bs-dropdown-min-width: auto;
  }




/* |||||||||DASHBOARD|||||||||| */

.card {
  margin-bottom: 1.875em;
  border-radius: 5px;
  padding: 0;
  border: 0px solid transparent;
  -webkit-box-shadow: 0 0 20px rgba(0, 0, 0, 0.08);
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.08);
}

.card .card-body {
  float: left;
  padding: 1.25em;
  position: relative;
  width: 100%;
}

.stat-widget-five {
  min-height: 60px;
}

.stat-widget-five .stat-icon {
  font-size: 40px;
  line-height: 40px;
  position: absolute;
  left: 30px;
  top: 20px;
}

.dib {
  display: inline-block;
}

.flat-color-1 {
  color: #00c292;
}

.flat-color-2 {
  color: #ab8ce4;
}

.flat-color-3 {
  color: #03a9f3;
}

.flat-color-4 {
  color: #fb9678;
}

.stat-widget-five .stat-content {
  margin-left: 70px;
}

.stat-widget-five .stat-text {
  color: #455a64;
  font-size: 20px;
}

.stat-widget-five .stat-heading {
  color: #99abb4;
  font-size: 14px;
}





/* Product Category Page */
.admin-page-heading
{
  text-align: center;
  margin-bottom: 30px;
}

.admin-table
{
  box-sizing: border-box;
  text-align: center;
}

.new-category-add
{
  box-sizing: border-box;
  text-align: center;
  margin-bottom: 30px;
}

.product-category-image
{
