/* |||||||||||||||||||||||||| Navbar Css |||||||||||||||||||||||||||||||*/

.navbar
{
  color: rgb(0, 0, 0);
  background-color: white;
  /* background: linear-gradient(90deg, rgba(0,0,0,1) 0%, rgba(0,37,5,1) 100%); */
  font-family: 'Open Sans', sans-serif;
  font-size: 15px;
  line-height: 20px;
  text-transform: uppercase;
  box-shadow: 0px 8px 6px -6px gray ;
  --webkit-box-shadow: 0px 8px 6px -6px gray ;
  border-bottom: 0.3px solid gray!important;
  position: sticky;
  top: 0;
  z-index: 1;
}

.navbar-brand
{
font-weight: 700!important;
color: rgb(0, 0, 0);
height: 70px;
}

